<template>
    <div class="table">
        <div class="container">
            <div class="handle-box">
                <el-form :inline="true" label-width="80px">
                    <el-form-item label="">
                        <el-select
                            v-model="parames.orderStatus"
                            placeholder=""
                            @change="getDataList"
                            class="handle-select"
                        >
                            <el-option key="-1" label="" :value="-1"></el-option>
                            <el-option key="0" label="" :value="0"></el-option>
                            <el-option key="1" label="" :value="1"></el-option>
                            <el-option key="2" label="" :value="2"></el-option>
                            <el-option key="3" label="" :value="4"></el-option>
                            <el-option key="4" label="" :value="5"></el-option>
                            <el-option key="5" label="" :value="6"></el-option>
                            <el-option key="6" label="" :value="7"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="parames.orderId"
                            placeholder=""
                            class="handle-input"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="parames.userMobile"
                            placeholder=""
                            class="handle-input"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="parames.goodsName"
                            placeholder=""
                            class="handle-input"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="parames.timeType"
                            placeholder=""
                            class="handle-select"
                        >
                            <el-option key="-1" label="" :value="-1"></el-option>
                            <el-option key="0" label="" :value="1"></el-option>
                            <el-option key="1" label="" :value="2"></el-option>
                            <el-option key="2" label="" :value="3"></el-option>
                            <el-option key="3" label="" :value="4"></el-option>
                            <el-option key="4" label="" :value="5"></el-option>
                        </el-select>
                        <el-date-picker
                            value-format="timestamp"
                            v-model="createDateList"
                            type="daterange"
                            start-placeholder=""
                            end-placeholder=""
                            @change="selectTime"
                        ></el-date-picker>
                    </el-form-item>
                    <el-button type="primary" @click="search"></el-button>
                    <el-button  @click="exportExcel">Excel</el-button>

                </el-form>
            </div>
            <el-table :data="dataList" border class="table">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-table :data="props.row.orderGoodsVoList" border class="table">
                            <el-table-column label="" min-width="100">
                                <template slot-scope="scope">
                                    <div class="goods-info">
                                        <div class="goods-img-wrap">
                                            <img class="goods-img" :src="scope.row.image" alt>
                                        </div>
                                        <div class="goods-title">{{scope.row.goodsName}}</div>
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column label="" min-width="60">
                                <template slot-scope="scope">
                                    <p>{{scope.row.reservePrice}}</p>
                                    <p
                                        v-if="scope.row.price!=scope.row.reservePrice"
                                    >{{scope.row.price}}</p>
                                </template>
                            </el-table-column>
                            <el-table-column label="sku" min-width="60">
                                <template slot-scope="scope">
                                    <div v-if="scope.row.skuPropertyList.length>0">
                                        <p
                                            v-for="item in scope.row.skuPropertyList"
                                            :key="item.goodsId"
                                        >{{item.propertyName}}:{{item.valueAlias}}</p>
                                    </div>
                                    <div v-else></div>
                                </template>
                            </el-table-column>
                            <el-table-column label="" prop="goodsNum" min-width="50"></el-table-column>
                        </el-table>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="orderId" min-width="70"></el-table-column>
                <el-table-column label="" prop="totalPrice" min-width="35"></el-table-column>
                <el-table-column label="" prop="goodsNum" min-width="35"></el-table-column>
                <el-table-column label="" min-width="60">
                    <template slot-scope="scope">
                        <p v-if="scope.row.acceptName">{{scope.row.acceptName}}</p>
                        <p v-if="scope.row.acceptMobile">{{scope.row.acceptMobile}}</p>
                        <p class="red" v-if="scope.row.remark">{{scope.row.remark}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="" min-width="70">
                    <template slot-scope="scope">
                        <p v-if="scope.row.createTimeStr">{{scope.row.createTimeStr}}</p>
                        <p v-if="scope.row.paidTimeStr">{{scope.row.paidTimeStr}}</p>
                        <p v-if="scope.row.deliveryTimeStr">{{scope.row.deliveryTimeStr}}</p>
                        <p v-if="scope.row.acceptTimeStr">{{scope.row.acceptTimeStr}}</p>
                        <p v-if="scope.row.refundTimeStr">{{scope.row.refundTimeStr}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="orderStatusStr" min-width="40"></el-table-column>
                <el-table-column label="" min-width="50">
                    <template slot-scope="scope">
                        <p
                            v-if="scope.row.deliveryOperateName"
                        >:{{scope.row.deliveryOperateName}}</p>
                        <p
                            v-if="scope.row.refundGoodsOperateName"
                        >:{{scope.row.refundGoodsOperateName}}</p>
                        <p v-if="scope.row.refundOperateName">:{{scope.row.refundOperateName}}</p>
                        <p
                            v-if="scope.row.orderStatusRemark"
                            class="red"
                        >:{{scope.row.orderStatusRemark}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="" min-width="80">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            icon="el-icon-lx-info"
                            class="grey"
                            @click="handleOrder('remark',scope.row)"
                        ></el-button>
                        <el-button
                            type="text"
                            icon="el-icon-download"
                            @click="handleOrder('shipments',scope.row)"
                            v-if="scope.row.orderStatus==1"
                        ></el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleOrder('shipments',scope.row)"
                            v-if="scope.row.orderStatus==2"
                        ></el-button>
                        <el-button
                            type="text"
                            icon="el-icon-lx-link"
                            @click="handleOrder('logistics',scope.row)"
                            v-if="scope.row.orderStatus==2"
                        ></el-button>
                        <el-button
                            type="text"
                            icon="el-icon-lx-recharge"
                            class="red"
                            @click="handleOrder('pay',scope.row)"
                            v-if="scope.row.orderStatus==1 || scope.row.orderStatus==6"
                        >{{scope.row.orderStatus==1?'':''}}</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-location-outline"
                            v-if="scope.row.orderStatus==1"
                            @click="handleOrder('location', scope.row)"
                        ></el-button>
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            v-if="scope.row.status!=3"
                            @click="handleOrder('del', scope.row)"
                        ></el-button>
                        <el-button
                            type="text"
                            class="red"
                            icon="el-icon-download"
                            @click="handleOrder('refund',scope.row)"
                            v-if="scope.row.orderStatus==5"
                        ></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <my-page :pager="pager" @change="changePage"></my-page>
            </div>
        </div>
        <edit-location v-if="showLocationBox"></edit-location>
        <shipments v-if="showShipmentsBox"></shipments>
        <refund v-if="showRefundBox"></refund>
        <pay v-if="showPayBox"></pay>
        <logistics v-if="showLogisticsBox"></logistics>


    </div>
</template>

<script>
import EditLocation from "../../components/common/orderOperation/location/index.vue";
import Shipments from "../../components/common/orderOperation/shipments.vue";
import Refund from "../../components/common/orderOperation/refund.vue";
import Pay from "../../components/common/orderOperation/pay.vue";
import Logistics from "../../components/common/orderOperation/logistics.vue";



export default {
    name: "orderList",
    components: {
        EditLocation,
        Shipments,
        Refund,
        Pay,
        Logistics
    },
    data() {
        return {
            createDateList: [], //
            pager: {
                total: 100,
                page: 1,
                rows: 20
            }, //

            dataList: [], //
            parames: {
                orderId: null,
                userMobile:null,
                goodsName:null,
                timeType: -1,
                startTime:-1,
                endTime:-1,
                orderStatus: -1, //-1= 0=1=2=4=5=20=30=
                pageNo: 1,
                pageSize: 20
            }, //
            orderId: null,
            showLocationBox: false, //
            showShipmentsBox: false, //
            showRefundBox: false, //
            showPayBox:false,// 
            showLogisticsBox:false,//
            returnOrderNo:"",//
            expressId:"",//Id
        };
    },
    created() {
        this.getDataList();
    },

    activated() {
        if (this.$route.params.onload) {
            if (this.$route.params.onloadDeep) {
                this.parames = {
                    goodsName: "",
                    goodsStatus: "-1",
                    timeOnSaleSt: -1,
                    timeOnSaleEd: -1,
                    pageNo: 1,
                    pageSize: 20
                };
            }
            this.getDataList();
        }
    },
    methods: {
        async getDataList() {
            const data = await this.$api.OrderList(this.parames);

            if (data && data[0] && data[0].pageNum == 1) {
                this.pager.total = data[0].total;
                this.pager.rows = data[0].pageSize;
            } else if (this.parames.pageNo == 1 && data.length == 0) {
                this.pager.total = 0;
            }

            this.dataList = data;
        },
        //
        selectTime() {
            this.parames.startTime = this.createDateList
                ? this.createDateList[0]
                : -1;
            this.parames.endTime = this.createDateList
                ? this.createDateList[1]
                : -1;
        },
        //
        search() {
            this.getDataList();
        },
        //
        exportExcel(){
            if(this.dataList.length>0){
                let str= `orderStatus=${this.parames.orderStatus}&goodsName=${this.parames.goodsName?this.parames.goodsName:''}&userMobile=${this.parames.userMobile?this.parames.userMobile:''}&orderId=${this.parames.orderId?this.parames.orderId:''}&orderType=${this.parames.orderType?this.parames.orderType:''}&timeType=${this.parames.timeType?this.parames.timeType:''}&endTime=${this.parames.endTime?this.parames.endTime:''}&startTime=${this.parames.startTime?this.parames.startTime:''}`
                window.open(`${process.env.VUE_APP_API_BASE}admin/order/export_order?${str}`)
            }else{
                this.$message.error('')
            }
        },
        //
        changePage() {
            this.parames.pageNo = this.pager.page == 0 ? 1 : this.pager.page;
            this.parames.pageSize = this.pager.rows;
            this.getDataList();
        },

        //
        handleOrder(status, row) {
            if (status === "remark") {
                this.$prompt("", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    inputValue: row.remark
                })
                    .then(({ value }) => {
                        if (value === "") {
                            this.$message({
                                message: "",
                                type: "warning"
                            });
                        } else {
                            this.orderRemark(row.orderId, value);
                        }
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: ""
                        });
                    });
            } else if (status === "del") {
                this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.goodsDel(row.goodsId);
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: ""
                        });
                    });
            } else if (status === "location") {
                this.orderId = row.orderId;
                this.showLocationBox = true;
            } else if (status === "shipments") {
                this.orderId = row.orderId;
                this.showShipmentsBox = true;
            } else if (status === "refund") {
                this.orderId = row.orderId;
                this.showRefundBox = true;
            } else if (status === "pay") {
                this.orderId = row.orderId;
                this.returnOrderNo=row.returnOrderNo
                this.showPayBox = true;
                
            } else if (status === "logistics") {
                this.orderId = row.orderId;
                this.expressId=row.wlOrder
                this.showLogisticsBox = true;
                
            }
        },
        //
        async orderRemark(orderId, remark) {
            const data = await this.$api.OrderRemark(
                { orderId, remark },
                orderId
            );
            this.$message({
                message: "",
                type: "success"
            });
            this.getDataList();
        },
        //
        async goodsDel(goodsId) {
            const data = await this.$api.GoodsDelete({ goodsId });
            this.$message({
                message: "",
                type: "success"
            });
            this.getDataList();
        }
    }
};
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 200px;
    display: inline-block;
}
.del-dialog-cnt {
    font-size: 16px;
    text-align: center;
}
.table {
    width: 100%;
    font-size: 14px;
}
.mr10 {
    margin-right: 10px;
}
.red {
    color: #ff0000;
}
.grey {
    color: #999;
}
</style>
