<template>
    <div class="shipments-wrap" @click.self="$parent.showShipmentsBox=false">
        <div class="shipments">
            <div class="shipments-header">
                {{title}}
                <i
                    class="el-icon-circle-close-outline"
                    style="font-size:26px;color:#888"
                    @click="$parent.showShipmentsBox=false"
                ></i>
            </div>
            <el-form label-width="80px" style="padding:10px 30px 0">
                <el-form-item label="">
                    <el-select v-model="parames.expressName" placeholder="">
                        <el-option
                            v-for="item in options"
                            :key="item.label"
                            :label="item.label"
                            :value="item.label"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="parames.expressId" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="parames.remark"></el-input>
                </el-form-item>
                <div style="display: flex;justify-content: center;">
                    <el-button size="mini" @click="$parent.showShipmentsBox=false"></el-button>
                    <el-button type="primary" size="mini" @click="updateShipments"></el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    name: "shipments",
    data() {
        return {
            options: [
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                },
                {
                    value: "FedEx",
                    label: "FedEx"
                },
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                }
            ],
            parames: {
                orderId:"",
                expressId: "",
                expressName: "",
                remark: ""
            },
            title:""
        };
    },
    mounted() {
        this.shipmentsDetail();
    },

    methods: {
        async shipmentsDetail() {
            const data = await this.$api.ShipmentsDetial( { orderId: this.$parent.orderId },this.$parent.orderId);
            if(data.expressId==null){
                this.title=""
            }else{
                this.title=""
            }
            this.parames.orderId=this.$parent.orderId
            this.parames.expressId=data.expressId
            this.parames.expressName=data.expressName
            this.parames.remark=data.remark

        },
        async shipmentsUpdate() {
            const data = await this.$api.ShipmentsUpdate(this.parames,this.$parent.orderId);
            this.$parent.showShipmentsBox = false;
            this.$message({
                message: "",
                type: "success"
            });
            this.$parent.getDataList();
        },
        updateShipments() {
            if (this.parames.expressName == "") {
                this.$message.error("");
                return;
            }
            if (this.parames.expressId == "") {
                this.$message.error("");
                return;
            }
            this.shipmentsUpdate()
            
        }
    }
};
</script>
<style scoped>
.shipments-wrap {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
}
.shipments {
    padding: 30px;
    background: #fff;
    border-radius: 10px;
}
.shipments-header {
    font-size: 20px;
    font-weight: 500px;
    border-bottom: 1px solid #eee;
    color: #666;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
}

</style>

